html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-size: 100%; }

html,
body {
  height: 100%;
  min-height: 100%;
  /* for firefox */
  width: 100%; }

body {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important; } }

@keyframes resize {
  0%,
  17.5% {
    width: 0; } }

td.star {
  width: 44px;
  display: inline-block; }
  @media only screen and (min-width: 768px) {
    td.star {
      display: table-cell; } }

td.title {
  width: calc(100% - 44px);
  display: inline-block;
  padding: 0 0.75rem; }
  @media only screen and (min-width: 768px) {
    td.title {
      padding: 0 0.25rem;
      width: 300px;
      display: table-cell; } }

td.body {
  display: block;
  padding-left: 2.5rem; }
  @media only screen and (min-width: 768px) {
    td.body {
      display: table-cell; } }
  td.body a {
    color: #1d5796; }
  td.body ul {
    padding-left: 20px; }

.favButton {
  all: unset;
  cursor: pointer;
  transition: 0.3s background-color cubic-bezier(0.17, 0.89, 0.32, 1.49);
  border-radius: 99px;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-tap-highlight-color: transparent; }
  .favButton svg {
    width: 24px;
    z-index: 5; }
  .favButton .starIcon {
    fill: #aaaaaa; }
  .favButton:hover, .favButton:focus {
    background-color: rgba(170, 170, 170, 0.2); }
    .favButton:hover .starIcon, .favButton:focus .starIcon {
      fill: none;
      stroke: #aaaaaa;
      stroke-width: 2px; }
  .favButton.favourited svg {
    will-change: width;
    animation: resize 0.3s cubic-bezier(0.17, 0.89, 0.32, 1.49); }
  .favButton.favourited:hover, .favButton.favourited:focus {
    background-color: rgba(67, 204, 133, 0.2); }
  .favButton.favourited .starIcon {
    fill: #23995c;
    stroke: none; }
