.favButton {
  all: unset;
  cursor: pointer;
  -webkit-transition: 0.3s background-color cubic-bezier(0.17, 0.89, 0.32, 1.49);
  transition: 0.3s background-color cubic-bezier(0.17, 0.89, 0.32, 1.49);
  border-radius: 99px;
  width: 44px;
  height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-tap-highlight-color: transparent; }
  .favButton svg {
    width: 24px;
    z-index: 5; }
  .favButton .starIcon {
    fill: #aaaaaa; }
  .favButton:hover, .favButton:focus {
    background-color: rgba(170, 170, 170, 0.2); }
    .favButton:hover .starIcon, .favButton:focus .starIcon {
      fill: none;
      stroke: #aaaaaa;
      stroke-width: 2px; }
  .favButton.favourited svg {
    will-change: width;
    -webkit-animation: resize 0.3s cubic-bezier(0.17, 0.89, 0.32, 1.49);
            animation: resize 0.3s cubic-bezier(0.17, 0.89, 0.32, 1.49); }
  .favButton.favourited:hover, .favButton.favourited:focus {
    background-color: rgba(67, 204, 133, 0.2); }
  .favButton.favourited .starIcon {
    fill: #23995c;
