html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-size: 100%; }

html,
body {
  height: 100%;
  min-height: 100%;
  /* for firefox */
  width: 100%; }

body {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important; } }

.StateInfo {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  color: #787878;
  padding: 2.5rem 0;
  max-width: 100%;
  margin: 0 auto;
  overflow: hidden;
  word-break: break-word; }
  @media only screen and (min-width: 1024px) {
    .StateInfo {
      max-width: 50%; } }
  .StateInfo h2 {
    margin-bottom: 0;
    color: #23995c; }

#SvgIconLoading {
  fill: #23995c; }

svg {
  width: 96px;
  fill: #aaaaaa; }
