html {
  position: relative;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
  font-size: 100%; }

html,
body {
  height: 100%;
  min-height: 100%;
  /* for firefox */
  width: 100%; }

body {
  margin: 0;
  padding: 0; }

*,
*::before,
*::after {
  box-sizing: border-box; }

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen-Sans, Ubuntu, Cantarell, "Helvetica Neue", sans-serif; }

.visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px; }

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none !important;
    transition: none !important; } }

.searchForm {
  display: flex;
  flex-direction: row;
  padding: 0.75rem 0; }
  @media only screen and (min-width: 1024px) {
    .searchForm {
      padding: 1.5rem 0; } }
  .searchForm .searchLabel {
    display: none; }
  .searchForm .searchInput {
    font-size: 1rem;
    padding: 1rem;
    border-radius: 4px;
    border: 1px solid #aaaaaa;
    flex: 1;
    margin-right: 1rem;
    transition: 0.3s box-shadow cubic-bezier(0.17, 0.89, 0.32, 1.49); }
    .searchForm .searchInput:focus {
      outline: none;
      border-width: 2px;
      border-color: #23995c;
      box-shadow: 0 6px 22px rgba(35, 153, 92, 0.2); }
    @media only screen and (min-width: 1024px) {
      .searchForm .searchInput {
        font-size: 1.25rem; } }
  .searchForm .searchButton {
    all: unset;
    -webkit-tap-highlight-color: transparent;
    padding: 0.25rem 0.75rem;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #23995c;
    border-radius: 4px;
    cursor: pointer;
    color: #ffffff;
    box-shadow: 0 2px 2px rgba(51, 51, 51, 0.15);
    transition: 0.3s box-shadow cubic-bezier(0.17, 0.89, 0.32, 1.49), 0.3s background-color cubic-bezier(0.17, 0.89, 0.32, 1.49); }
    @media only screen and (min-width: 1024px) {
      .searchForm .searchButton {
        padding: 0.75rem; } }
    .searchForm .searchButton svg {
      fill: #ffffff;
      width: 24px; }
      @media only screen and (min-width: 1024px) {
        .searchForm .searchButton svg {
          width: 36px; } }
    .searchForm .searchButton:hover, .searchForm .searchButton:focus {
      background-color: #0d6638;
      box-shadow: 0 6px 22px rgba(35, 153, 92, 0.5); }
    .searchForm .searchButton:active {
      background-color: #0a4f2c;
      box-shadow: 0 2px 2px rgba(51, 51, 51, 0.15); }
